@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Noto Sans', sans-serif;
}
.main{
  width: 100vw;
  height: 100vh;
  background-color: #201b2c;
  display: flex;
  justify-content: center;
  align-items: center;
}

.left-section{
  width: 50vw;
  height: 100vh;
  background-image: url(/static/media/medicine-animate.a935c724.svg);
  background-size: 60%;
  background-repeat: no-repeat;  
  background-position: center; 
  
}

.left-section > h1{
  color: #77ffc0;
  margin-top: 8%;
  margin-left: 25%;
  font-size: 60px;
  font-weight: bolder;
}

.right-section{
  width: 50vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}




.login-form{
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 30px 35px;
    background: #2f2841;
    border-radius: 20px;
    box-shadow: 0px 10px 40px #00000056;
    background-image: url('https://i.imgur.com/HBDe1Wd.png');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }
  
  .login-form > h1 {
    color: #00ff88;
    font-weight: 800;
    margin: 0;
  }
  
  .textfield{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin: 10px 0px;
  }
  
  .textfield > input{
    width: 100%;
    border: none;
    border-radius: 10px;
    padding: 15px;
    background-color: #514869;
    color: #f0ffffde;
    font-size: 12pt;
    box-shadow: 0px 10px 40px #00000056;
    outline: none;
    box-sizing: border-box;
  }
  
  .textfield > input > ::-webkit-input-placeholder{
    color: #f0ffff94;
  }
  
  .textfield > input > ::placeholder{
    color: #f0ffff94;
  }
  
  .textfield > label{
    color: #f0ffffde;
    margin-bottom: 10px;
  }
  
  .btn-login{
    width: 100%;
    padding: 16px 0px;
    margin: 25px;
    border: none;
    border-radius: 8px; 
    outline: none;
    text-transform: uppercase;
    font-weight: 800;
    letter-spacing: 3px;
    color: #2b134b;
    background-color: #00ff88;
    cursor: pointer;
    box-shadow: 0px 10px 40px -12px #00ff8052;
  }
  
  .btn-cadastrar{
    color: #00ff88;
    cursor: pointer;
    display: flex;
    align-items: center;
  }
  
  .btn-login:hover{
    box-shadow: 0px 10px 40px 2px #00ff8052;
  }
  
  
